.navbarr {
    position:fixed;
    z-index:1;
    background: rgb(44, 45, 49);
    height: 80px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

}

.search {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 80%;
}

.inputt {
    width: 90%;
    height: 40px;
    padding:0 25px;
    border-radius: 50px;
    border: none;
    font-size: 20px;
    color: #303232;
    background-color:#eeeeee;
    font-weight: 100;
}

input::-webkit-input-placeholder {
    color: #9dabb1;
}

input::placeholder {
    color: #9dabb1;
}

input:focus {
    outline: none;
    box-shadow: 0 5px 25px rgba(0, 0, 0, 0.271);
}

.buttonn {
    width: 100px;
    margin-right: 20px;
    background:#73d101;
    color: #fff;
    height: 40px;
    border: none;
    border-radius: 50px;
    font-size: 20px;
}
.cardd {
    width: 100%;
    height:100%;
    background: rgba(157, 157, 157, 0.539);
    position: relative;
}

img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.holder {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
}

.info {
    position: absolute;
    color: #fff;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    display: none;
    text-align: center;
    justify-content: center;
    z-index: 1;
    font-size: 20px;
    width: 100%;
}

.cardd:hover .holder {
    transition: all .3s;
    background: rgba(19, 19, 19, 0.249);
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
}

.cardd:hover .info {
    display: block;
}
.movieslist{
    margin:50px auto;
    padding-top: 50px;
    width: 80%;
    height: -webkit-max-content;
    height: max-content;
    background-color:rgb(255, 255, 255);
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px ,1fr));
    grid-gap: 20px;
    border-radius: 50px;
}
.containerr{
    background:rgb(254, 254, 254);
    padding: 10px;
    height:100vh;
    width: 100%;
}

